<template>
  <div class="container-fluidd">
    <div class="container text-light" id="slogan">
      <img src="./assets/one.gif" width="200" height="100" class="img-fluid mt-4">
      <div style="padding-top: 50px">
        <h1>NameTech</h1>
        <h6>Gerador de nomes com Vue.js, <br> GraphQL e Node</h6>
      </div>
    </div>
    <!-- <hr> -->
    <div id="main">
      <div class="container">
        <div class="row">
          <!-- ====================== COLUNA 1: Prefixos ====================== -->
          <div class="col-md">
            <h4 class="mt-4">Prefixos <span class="badge badge-info">{{prefixes.length}}</span></h4>
            <div class="card">
              <div class="card-body">
                <ul class="list-group">
                  <li class="list-group-item" v-for="prefix in prefixes" v-bind:key="prefix">
                    <div class="row">
                      <div class="col-6 mt-1">
                        {{prefix}}
                      </div>
                      <div class="col-6 text-right">
                        <button class="btn btn-info" v-on:click="deletePrefix(prefix)">
                          <span class="fa fa-trash"></span>
                        </button>
                      </div>
                    </div>
                  </li>
                </ul>
                <br>
                <div class="input-group">
                  <input type="text" placeholder="Digite o prefixo" class="form-control" v-model="prefix" v-on:keyup.enter="addPrefix(prefix)">
                  <div class="input-group-append">
                    <button class="btn btn-info" v-on:click="addPrefix(prefix)">
                      <span class="fa fa-plus"></span>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- ====================== COLUNA 2: Sufixos ====================== -->
          <div class="col-md">
            <h4 class="mt-4">Sufixos  <span class="badge badge-info">{{sufixes.length}}</span></h4>
            <div class="card">
              <div class="card-body">
                <ul class="list-group">
                  <li class="list-group-item" v-for="sufix in sufixes" v-bind:key="sufix">
                    <div class="row">
                      <div class="col-6 mt-1">
                        {{sufix}}
                      </div>
                      <div class="col-6 text-right">
                        <button class="btn btn-info" v-on:click="deleteSufix(sufix)">
                          <span class="fa fa-trash"></span>
                        </button>
                      </div>
                    </div>
                  </li>
                </ul>
                <br>
                <div class="input-group">
                  <input type="text" placeholder="Digite o sufixo" class="form-control" v-model="sufix" v-on:keyup.enter="addSufix(sufix)">
                  <div class="input-group-append">
                    <button class="btn btn-info" v-on:click="addSufix(sufix)">
                      <span class="fa fa-plus"></span>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md">
            <h4 class="mt-4">Domínios <span class="badge badge-info">{{domins.length}}</span></h4>
            <div class="card">
              <div class="card-body">
                <ul class="list-group">
                  <li class="list-group-item" v-for="dominio in domins" v-bind:key="dominio">
                    {{dominio}}
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import "bootstrap/dist/css/bootstrap.css"
  import "font-awesome/css/font-awesome.css"
  import "animate.css/animate.css"

  export default {
    name: 'app',
    data: ()=>{
      return {
        prefix: "",
        sufix: "",
        prefixes: ['Art', 'Rocket', 'Assemble'],
        sufixes: ['Tech', 'Sky', 'Avengers'],
        domins: []
      }
    },
    methods: {
      addPrefix(prefix){
        if(this.prefix == ""){alert("Insira um prefixo válido!")}
        else{
          this.prefixes.push(prefix);
          this.gerarDominios()
          this.prefix = ""
        }        
      },      
      addSufix(sufix){
        if(this.sufix == ""){alert("Insira um sufixo válido!")}
        else{
          this.sufixes.push(sufix);
          this.gerarDominios()
          this.sufix = ""
        }
      },
      deletePrefix(prefix){
        this.prefixes.splice(this.prefixes.indexOf(prefix),1)
        this.gerarDominios()
      },
      deleteSufix(sufix){
        this.sufixes.splice(this.sufixes.indexOf(sufix),1)
        this.gerarDominios()
      },
      gerarDominios(){
        this.domins = []
        for(const prefix of this.prefixes){
          for(const sufix of this.sufixes){
            this.domins.push(prefix + sufix)
          }
        }
      }
    }
  }
</script>

